









import 'reflect-metadata';
import {Vue, Component, Prop, Watch} from 'vue-property-decorator';

import Modal from './Modal.vue';
import CopyText from "../misc/CopyText.vue";
import QRCode from 'qrcode'
import {AVMKeyPair} from "avalanche/dist/apis/avm";

@Component({
    components: {
        Modal,
        CopyText
    }
})
export default class QRModal extends Vue{
    colorDark:string = "#242729";
    colorLight:string = "#FFF";

    @Watch('address', { immediate: true })
    onaddrchange(val:string){
        if(val){
            this.updateQR()
        }
    }


    @Watch('$root.theme', {immediate: true})
    onthemechange(val:string){
        if(val==='night'){
            this.colorDark = "#E5E5E5";
            this.colorLight = "#242729";
        }else{
            this.colorDark = "#242729";
            this.colorLight = "#FFF";
        }
        this.updateQR();
    }

    open(){
        // @ts-ignore
        this.$refs.modal.open();
    }
    updateQR(){
        if(!this.address) return;
        let canvas = this.$refs.qr;
        QRCode.toCanvas(canvas, this.address, {
            scale: 6,
            color: {
                light: this.colorLight,
                dark: this.colorDark
            }
        }, function (error) {
            if (error) console.error(error);
            // console.log('success!');
        })
    }

    get address(){
        let activeKey:AVMKeyPair|null = this.$store.getters.activeKey;
        if(!activeKey){
            return '-'
        }
        return activeKey.getAddressString();
    }

    mounted() {
        this.updateQR();
    }
}
